// Colors
$bg-color: #1370fb;
$background-color: rgba(64, 64, 64, 1);

* {
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
}

body {
	background-color: $background-color;
	font-family:  "Share Tech", sans-serif;
	font-weight: 400;
}

.wrapper {

	min-height: 100vh;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: center;
	-ms-flex-pack: center;
	justify-content: center;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	padding: 2rem;

	.card {
		width: 25rem;
		height: 41rem;
		padding: 20px;
		background-color: white;
		box-shadow: 0 3px 16px rgba(0, 0, 0, 0.16), 0 3px 16px rgba(0, 0, 0, 0.23);
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		-webkit-box-orient: vertical;
		-webkit-box-direction: normal;
		-ms-flex-direction: column;
		flex-direction: column;

		&__header {
			height: 4rem;
			background-color: white;
			display: -webkit-box;
			display: -ms-flexbox;
			display: flex;
			-webkit-box-pack: start;
			-ms-flex-pack: start;
			justify-content: flex-start;
			flex-direction: row;
			padding: 0.5rem;

			&_title {
				flex-grow: 20;

				h4 {
					margin: 0;
				}

				h5 {
					margin: 2px 0;
				}
			}

			&_url {
				flex-grow: 2;
				// border-bottom: 1px solid #ccc;

				.input {
					float: left;
					// margin-top: 20px;

					input {
						border: 1px solid #d3d3d3;
						background: transparent;
					}

				}
			}
		}

		&__content {
	      flex-basis: 30rem;
	      display: flex;
	      align-items: center;
	      flex-direction: column;
	      flex-wrap: wrap;
	      padding-top: 20px;

	      .outline {
	      	display: flex;
	      	align-items: center;
	      	flex-direction: column;
	      	border: 3px solid black;
	      	width: 23rem;
	      	padding: 5px;
	      	border-radius: 10px;
	      	margin-bottom: 20px;
	      	background: black;
	      	// background-color: green;
			
			canvas, #qr {
				border-radius: 20px;
				margin-top: 10px;
			}

			.bottom {
				font-size: 4rem;
				// color: #e41847;
				color: white;
				margin-top: 10px;
			}

	      	img {
	      		// width: 5rem;
	      		margin-top: 20px;
	      	}
	      }
	  	}
	}
}